:root {
  --primary-font-color: #fff;
  --secondary-font-color: #d41443;
  --third-font-color: #ffa5ba;
  --dark-font-color: #000;
  --grey-font-color: #907e82;
  --accent-color: #d41443;
  --button-color: #d41443;
  --button-light-color: #fff;
}

h1, h2, h3, h4, h5, h6, ol, ul, a, p {
  margin: 0;
  padding: 0;
}

ol, ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  display: block;
}

body {
  font-family: DM Sans, sans-serif;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 768px) {
  .container {
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    padding-left: 16px;
    padding-right: 16px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.header-hero {
  background: #ffb8ca;
}

.header__container {
  justify-content: space-between;
  padding-top: 8px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__container {
    padding-top: 32px;
  }
}

