}

.header__button-box {
  align-items: center;
  display: flex;
}

.button-menu {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .button-menu {
    margin-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .button-menu {
    display: none;
  }
}

.button-menu.is-open {
  z-index: 100;
  margin-right: 0;
  position: fixed;
  top: 20px;
  right: 20px;
}

.button-menu .button-menu__close, .button-menu.is-open .button-menu__burger {
  display: none;
}

.button-menu.is-open .button-menu__close {
  display: block;
}

.mobile-menu {
  z-index: 50;
}

@media screen and (max-width: 1199px) {
  .mobile-menu {
    width: 100%;
    height: 100%;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    background-color: rgba(0, 0, 0, 0);
    transition: transform .25s linear;
    position: fixed;
    top: 0;
    right: 0;
    transform: translateX(100%);
  }

  .mobile-menu.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: initial;
    transform: translateX(0);
  }
}

.mobile-menu__content {
  display: flex;
}

@media screen and (min-width: 1200px) {
  .mobile-menu__content {
    align-items: center;
  }
}

@media screen and (max-width: 1199px) {
