    height: 100%;
    width: 250px;
    background-color: #e17992;
    flex-direction: column-reverse;
    justify-content: flex-end;
    padding-top: 60px;
    padding-left: 20px;
    padding-right: 20px;
    position: fixed;
    top: 0;
    right: 0;
  }
}

