  .navigation__item:before {
    display: none;
  }
}

.navigation__item--last:after {
  content: "";
  width: calc(100% + 40px);
  height: 1px;
  background-color: #e18298;
  display: block;
  position: absolute;
  bottom: 0;
  left: -20px;
}

